body.single-tournaments {
  .grid {
    padding: 7.5rem $sidePadding;
    display: flex;
    flex-wrap: wrap;
    .content {
      width: calc(100% - 37.5rem);
      flex: 0 0 calc(100% - 37.5rem);
      padding-right: 15rem;
      .tabs {
        &-tab {
          &-header {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            align-content: center;
            justify-content: flex-start;
            margin-bottom: 3rem;
            &-item {
              position: relative;
              padding-bottom: 0.5rem;
              opacity: 0.5;
              transition: all 0.3s ease;
              cursor: pointer;
              &:not(:last-of-type) {
                margin-right: 1.5rem;
              }
              &::after {
                position: absolute;
                content: "";
                width: 100%;
                height: 0.2rem;
                bottom: 0;
                left: 0;
                background: $teal;
                @include transform(scaleX(0));
                @include transit(all 0.3s ease);
                transform-origin: center left;
              }
              &:hover {
                opacity: 1;
              }
              &.active {
                opacity: 1;
                &::after {
                  @include transform(scaleX(1));
                }
              }
            }
          }
          &-content {
            &-item {
              display: none;
              &.active {
                display: block;
              }
              .lineup-wrapper {
                li {
                  font-size: 3.2rem;
                  font-weight: 500;
                  color: #fff;
                  display: flex;
                  align-items: center;
                  align-content: center;
                  justify-content: space-between;
                  flex-wrap: wrap;
                  border-bottom: 1px solid $dblue;
                  padding: 1.5rem;
                  &:first-of-type {
                    border-top: 1px solid $dblue;
                  }
                  &:hover,
                  &.active {
                    background: $dblue;
                  }
                  a {
                    color: #fff;
                    transition: all 0.3s ease;

                    &:hover {
                      color: $teal;
                    }
                    &.active {
                      color: $teal;
                      @include rotate(180);
                    }
                  }
                }
                .lineup-members {
                  display: none;
                  flex: 0 0 100%;
                  width: 100%;
                  &-list {
                    li {
                      justify-content: flex-start;
                      border: none !important;
                      .img-wrapper {
                        width: 4rem;
                        height: 4rem;
                        margin-right: 1.5rem;
                        @include borderRadius(50%);
                        overflow: hidden;
                        img {
                          @include fullSizeImg(cover);
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    .sidebar {
      width: 37.5rem;
      flex: 0 0 37.5rem;
      position: relative;
      height: inherit;
    }
    .tournament-info-card {
      padding: 2rem;
      background: $dblue;
      @include borderRadius(1.6rem);
      width: 100%;
      position: sticky;
      top: 7.5rem;
      ul {
        li {
          display: flex;
          align-items: center;
          align-content: center;
          justify-content: space-between;
          flex-wrap: wrap;
          color: #fff;
          line-height: 1.5;
          p {
            font-size: 1.4rem;
          }
          &:not(:last-of-type) {
            margin-bottom: 3rem;
          }
        }
      }
    }
  }
}
